    background: linear-gradient(rgba(0,0,0,.7), rgba(0,0,0,.7)), url("https://realdeals.eu.com/wp-content/uploads/2018/09/iStock-6962024482.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
